@import "common.css";

.no-visible {
	display: none;
}
.visible {
	display: block;
}
.container {
	max-width: 1080px;
	margin: 0 auto;
	overflow: hidden;
}
section {
	overflow: hidden;
}
main {
	overflow: hidden;
}
@media(max-width: 1024.9px) {
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}
}
.sign_form {
	position: fixed;
	width: 400px;
	height: 400px;
	background-color: rgba(100, 100, 100, 0.5);
	z-index: 1;
	left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
	display: none;
	
}
@media(max-width: 425.9px) {
	.sign_form {
		width: 200px;
		height: 300px;
	}
}
.sign_form form {
	display: grid;
	padding-left: 20px;
	padding-right: 20px;
	justify-content: center;
	align-content: center;
	margin-top: 100px;


}
.sign_form form input {
	width: 100%;
	height: 50px;
	font-size: 15px;
	background: transparent;
	color: white;
	border-bottom: 2px solid black;
}
.sign_button {
	color: white;
	font-size: 12px;
	text-transform: uppercase;
	background-color: black;
	padding: 16px 32px 16px 32px;
	border-radius: 32px;
	margin-top: 10px;
}
.button_close_sign {
	position: absolute;
	top: 20px;
	right: 20px;
	background: transparent;
}
.nav {
	display: flex;
	justify-content: space-between;
	background: #FAFAFA;
	border-radius: 16px;
	height: 70px;
	align-items: center;
}
.nav li:first-child {
	display: flex;
	padding-left: 10px;
}
.nav li:first-child p a {
	font-size: 14px;
}
.nav li:first-child p:not(last-child) {
	margin-right: 32px;
}
.nav li:last-child {
	margin-right: 10px;
}
.button_sign_nav {
	color: white;
	font-size: 12px;
	text-transform: uppercase;
	background-color: black;
	padding: 16px 32px 16px 32px;
	border-radius: 32px;
} 
.main-photos {
	max-width: 750px;
	margin: 0 auto;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 78px;
	
}	
.burger_menu {
	position: fixed;
	height: 100%;
	width: 400px;
	left: 0;
	top: 0;
	background-color: rgba(0, 0, 0, 0.5);
	display: grid;
	text-align: center;
	justify-content: center;
	align-content: center;
	z-index: 1;
}
.burger_menu_max {
	display: none;
}
.burger_button {
	display: none;
}
.burger_text {
	font-size: 14px;
	color: white;
}
a .burger_text {
	margin-top: 20px;
}
.button_close {
	background: transparent;
}
.burger_menu img {
	position: absolute;
	top: 20;
	right: 20;
}
.burger_sign {
	display: inline-block;
	width: 114px;
	margin-top: 20px;
	color: white;
	font-size: 12px;
	text-transform: uppercase;
	background-color: black;
	padding: 16px 22px 16px 22px;
	border-radius: 32px;
}
.aaaa {
	display: none;
}
main .container h1 {
	margin-top: 42px;
	font-size: 142px;
}
.main_text {
	max-width: 453px;
	font-size: 18px;
	line-height: 27px;
	margin-top: 57px;
}
.sign {
		color: white;
	font-size: 12px;
	text-transform: uppercase;
	background-color: #C6F572;
	padding: 16px 62px 16px 62px;
	border-radius: 40px;
	margin-top: 47px;
}
@media(max-width: 1024.9px) {
	main .container h1 {
		font-size: 100px;
	}
}

@media(max-width: 650px) {
	.main-photos {
		display: grid;
		justify-content: center;
		align-items: center;

	}
	.main-photos img:last-child {
		margin-top: 30px;
	}
}
@media(max-width: 425.9px) {
	main .container h1 {
		font-size:  60px;
	}
	.nav li p {
		display: none;
	}
	.nav li:nth-child(2) {
		display: none;
	}
	.burger_button {
		display: block;
	}
	.aaaa {
	display: block;
}
}
@media(max-width: 375.9px) {
	.main-photos img:first-child {
		width: 300px;
	}
	.main-photos img:last-child {
		width: 150px;
	}
	.burger_menu {
		width: 300px;
	}
}
